import * as React from 'react';
import {
  JsonML,
  getTagName,
  getAttributes,
  MAPPING, 
  parseJsonMLToValue,
} from '@ali/4ever-utils';
import { CommonLine } from '@ali/we-design';

export default {
  name: 'hr',

  match: (node: JsonML): boolean => {
    return getTagName(node) === 'hr';
  },

  convert: (node: JsonML, path: number[]): React.ReactElement => {
    const payload = getAttributes(node);
    const parsedPayload = parseJsonMLToValue(payload as any, MAPPING);
    // @ts-ignore
    return <CommonLine data-path={path} {...parsedPayload} />;
  },
};
